.datepicker{position:absolute;top:100%;width:315px;z-index:5;background-color:#fff;box-shadow:0 14px 45px rgba(0,0,0,0.25),0 10px 18px rgba(0,0,0,0.22)}.datepicker__header{background-color:#ab1919;color:#FFF;padding:20px;height:100px}.datepicker__year{position:relative;overflow:hidden;opacity:0.7;height:16px;margin-bottom:10px;line-height:16px}.datepicker__date{position:relative;overflow:hidden;height:32px;font-size:32px;line-height:32px}.datepicker__week{font-size:12px;line-height:12px;color:rgba(0,0,0,0.8);padding:0 14px}.datepicker__weekday{float:left;width:41px;text-align:center}.datepicker__days{position:relative;overflow:hidden;margin:14px 14px 0;height:205px;-webkit-transition:height 0.3s;transition:height 0.3s}.datepicker__days.has-6-weeks{height:246px}.datepicker__day{position:relative;width:41px;height:41px;float:left;text-align:center;line-height:41px;cursor:pointer;-webkit-transition:color 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;transition:color 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms}.datepicker__day__effect{position:absolute;top:2px;left:2px;width:36px;height:36px;border-radius:50%;background-color:rgba(223,28,28,0.8);-webkit-transition:all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;transition:all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;-webkit-transform:scale(0);transform:scale(0);opacity:0}.datepicker__day__text{position:relative}.datepicker__day:hover{color:#FFF}.datepicker__day:hover .datepicker__day__effect{-webkit-transform:scale(1);transform:scale(1);opacity:0.6}.datepicker__day.selected{color:#FFF}.datepicker__day.selected .datepicker__day__effect{-webkit-transform:scale(1);transform:scale(1);opacity:1}.datepicker__controls{position:relative;height:56px;line-height:56px;text-align:center}.datepicker__controls button{position:relative;border:none;background-color:transparent;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;outline:none;cursor:pointer;width:56px;height:56px}.datepicker__controls svg{width:24px;height:24px;fill:rgba(0,0,0,0.87);vertical-align:middle}.datepicker__month{position:absolute;height:16px;line-height:16px;overflow:hidden;top:20px;left:0;right:0;bottom:0}.datepicker__next{float:right}.datepicker__prev{float:left}.datepicker__actions{text-align:right;padding:8px}.datepicker__actions button{border:none;background-color:transparent;display:inline-block;cursor:pointer;outline:none;color:rgba(223,28,28,0.8);font-size:14px;text-transform:uppercase;font-weight:500;min-width:88px;line-height:36px;text-align:center;-webkit-appearance:none;-webkit-transition:all 0.3s;transition:all 0.3s}.datepicker__actions button:hover{background-color:rgba(223,28,28,0.8)}.datepicker-slide-transition{opacity:1;-webkit-transition:all 0.3s;transition:all 0.3s;-webkit-transform:translateY(0);transform:translateY(0)}.datepicker-slide-leave,.datepicker-slide-enter{opacity:0;-webkit-transform:translateY(-15px);transform:translateY(-15px)}.slidev-transition{position:absolute;top:0;right:0;bottom:0;left:0;-webkit-transition:all 0.3s;transition:all 0.3s;-webkit-transform:translateX(0);transform:translateX(0);opacity:1}.slidev-transition.off{-webkit-transition-duration:0s;transition-duration:0s}.slidev-leave,.direction-prev.slidev-enter{-webkit-transform:translateX(-100%);transform:translateX(-100%);opacity:0}.slidev-enter,.direction-prev.slidev-leave{-webkit-transform:translateX(100%);transform:translateX(100%);opacity:0}.slideh-transition{position:absolute;top:0;right:0;bottom:0;left:0;-webkit-transition:all 0.3s;transition:all 0.3s;-webkit-transform:translateY(0);transform:translateY(0);opacity:1}.slideh-transition.off{-webkit-transition-duration:0s;transition-duration:0s}.slideh-leave,.direction-prev.slideh-enter{-webkit-transform:translateY(-100%);transform:translateY(-100%);opacity:0}.slideh-enter,.direction-prev.slideh-leave{-webkit-transform:translateY(100%);transform:translateY(100%);opacity:0}



