.timepicker{position:absolute;top:100%;width:210px;z-index:5;background-color:#fff;box-shadow:0 14px 45px rgba(0,0,0,0.25),0 10px 18px rgba(0,0,0,0.22)}.timepicker__header{background-color:#ab1919;color:#FFF;padding:20px;height:55px}.timepicker__time{padding:15px;margin-left:40px;margin-top:10px}.timepicker__time div{display:inline}.timepicker__time input{width:40px;height:40px;border:none;font-size:32px;color:#ab1919}.timepicker__time input:focus{outline:none}.timepicker__hour:after{content:':'}.timepicker__minute{margin-left:5px}.timepicker__actions{text-align:right;padding:8px}.timepicker__actions button{border:none;background-color:transparent;display:inline-block;cursor:pointer;outline:none;color:rgba(223,28,28,0.8);font-size:14px;text-transform:uppercase;font-weight:500;min-width:88px;line-height:36px;text-align:center;-webkit-appearance:none;-webkit-transition:all 0.3s;transition:all 0.3s}.timepicker__actions button:hover{background-color:rgba(223,28,28,0.8)}



